.burger-menu {
  position: relative;
}

.menu-button {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 3;
}

.burger-icon,
.burger-icon::before,
.burger-icon::after {
  content: '';
  display: block;
  width: 25px;
  height: 3px;
  background-color: black;
  position: relative;
}

.burger-icon::before,
.burger-icon::after {
  position: absolute;
  width: 100%;
  height: 100%;
}

.burger-icon::before {
  top: -10px;
}

.burger-icon::after {
  top: 10px;
}

.burger-icon.open {
  transform: rotate(45deg);
}

.burger-icon.open::before {
  transform: rotate(-90deg);
  top: 0;
}

.burger-icon.open::after {
  display: none;
}

.menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}

.menu-overlay ul {
  list-style: none;
  padding: 0;
}

.menu-overlay li {
  margin-bottom: 20px;
  font-size: 1.5em;
  color: white;
  cursor: pointer;
} 